import { Alert, AlertTitle, Collapse } from "@mui/material";
import { useFormData } from "./form_data";

function FormErrors({ ...props }) {
    const { errors } = useFormData();

    return (
        <Collapse in={Object.keys(errors).length > 0}>
            <Alert color="error" icon={false} {...props}>
                <AlertTitle>خطای اعتبارسنجی</AlertTitle>
                <ul>
                    {Object.keys(errors).map((key, index) => (
                        <li key={index}>{errors[key][0]}</li>
                    ))}
                </ul>
            </Alert>
        </Collapse>
    )
}

export default FormErrors;