import { Box, Button, Card, Stack } from "@mui/material";
import { useAuth } from "../../../../auth";
import { useFormData } from "./form_data";
import FormErrors from "./FormErrors";

export default function Operations({ injectedHooks }) {
    const { inject } = useAuth();
    const { register, handleSubmit, loading, reset, getValue, setValue } = useFormData();

    return (
        <Card variant="outlined">
            <Box sx={{ p: 2 }}>
                <FormErrors sx={{ mb: 2 }} />
                <Stack alignItems="end">
                    {inject('Invoices', 'InvoiceForm.Payment', { register, loading, getValue, setValue, reset, ...injectedHooks })}
                    <Button
                        variant="contained"
                        // size="large"
                        // fullWidth
                        // disabled={loading}
                        onClick={handleSubmit}
                    >ثبت فاکتور</Button>
                </Stack>
            </Box>
        </Card>
    )
}